import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ApiModule } from 'src/app/sdk/hr/api.module';
import { ApiModule as ApiModuleUser } from 'src/app/sdk/user/api.module';
import { ApiModule as ApiModulePayroll } from 'src/app/sdk/payroll/api.module';
import { ApiModule as ApiModuleDoc } from 'src/app/sdk/document/api.module';

import { ProfileRoutingModule } from './profile-routing.module';
import { ProfileComponent } from './profile.component';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { NzLayoutModule, NgZorroAntdModule, NzRadioModule } from 'ng-zorro-antd';
import { LayoutModule } from 'src/app/components/layout/layout.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { TrainingComponent } from './lainnya/training/training.component';
import { ProyekComponent } from './lainnya/proyek/proyek.component';
import { PeringatanComponent } from './lainnya/peringatan/peringatan.component';


@NgModule({
    declarations: [ProfileComponent, TrainingComponent, ProyekComponent, PeringatanComponent],
    imports: [
        CommonModule,
        ProfileRoutingModule,
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        NzRadioModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        ApiModulePayroll.forRoot({ rootUrl: 'http://erp-payroll.vm.devetek.com/api' }),
        ApiModuleUser.forRoot({ rootUrl: 'http://erp-user.vm.devetek.com/api' }),
        ApiModuleDoc.forRoot({ rootUrl: 'http://erp-document.vm.devetek.com/api' }),
    ],
    providers: [FormBuilderTypeSafe],
})
export class ProfileModule { }
